:root {
    /*Color Themes*/
    --bg-themes: #f7f7fd;
    --text-themes: #080C12;
    --text-title: #515A6E;
    --btn-themes: #034EA2;
    --btn-themes-light: #D6E9FF;
    --btn-themes-light-hover: #ADD3FF;
    --btn-themes-light-active: #99C9FF;
    --text-themes-light: #064996;
    --text-orange-themes: #F59356;
    --text-primary-themes: #0066B2;
    --text-red-themes: #FF3C31;
    --btn-default: #E8EAEC;
    --btn-default-hover: #DDE0E3;
    --btn-default-active: #C7CCD1;
    --btn-default-light-hover: #f5f5ff;
    --btn-default-light-active: #f8f5ff;
    --text-default: #212733;
    --bg-popup: #F7F7FD;
    --text-link: #1983FC;
    --text-link-hover: #0a58ca;
    --btn-themes-hover: #03438C;
    --btn-themes-active: #023978;
}


/*Min Width && Max Width*/

$index: 50;
@while $index <=500 {
    .min-w#{$index} {
        min-width: #{$index}px !important;
    }
    .max-w#{$index} {
        max-width: #{$index}px !important;
    }
    .w#{$index} {
        width: #{$index}px !important;
    }
    $index: ($index + 10);
}


/*Spacing Margin && Padding*/

$index: 10;
@while $index <=50 {
    .ps-#{$index} {
        padding-left: #{$index}px !important;
    }
    .pe-#{$index} {
        padding-right: #{$index}px !important;
    }
    $index: ($index + 5);
}