/*Custom Modal*/

.modal {
    z-index: 9999;
}

.modal-custom {
    .modal-footer {
        border-top: 0;
        padding-top: 0;
    }
}

.processing-file {
    .modal-dialog {
        max-width: 1200px;
    }
    .modal-body {
        height: calc(100vh - 180px);
        overflow: auto;
    }
    .box-data {
        .node {
            &.node-a {
                &:before {
                    background-color: #ff7f0e;
                }
            }
            &.node-b {
                &:before {
                    background-color: #d62728;
                }
            }
            &.node-c {
                &:before {
                    background-color: #2ca02c;
                }
            }
            &.node-d {
                &:before {
                    background-color: #1f77b4;
                }
            }
            &.node-e {
                &:before {
                    background-color: #33ccff;
                }
            }
        }
    }
}

.add-kkts{
    .modal-dialog{
        max-width: calc(100% - 30px);
    }
}