.navbar {
    padding: 15px 10px;
    background: #0B2C52;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    .dropdown-menu {
        left: auto;
        right: 0;
    }
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none
}

.line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd
}

.wrapper {
    display: flex;
    width: 100%;
    align-items: stretch;
    &.active {
        /*Nav*/
        #sidebar {
            max-width: 56px;
            min-width: 56px;
            top: 48px;
            .sidebar-header {
                display: none !important;
            }
            .box-info {
                margin-top: 1rem;
                img {
                    height: 48px;
                }
            }
            .name,
            .employee-number {
                display: none;
            }
            ul {
                &.components {
                    padding: 0 0 20px 0;
                    .text-menu {
                        font-size: 0;
                        display: none;
                    }
                    .pageSubmenu {
                        position: fixed;
                        left: 56px;
                        background: #fff;
                        border: 1px solid #ededed;
                        li {
                            a {
                                padding: 0;
                            }
                            .node-collaspe {
                                justify-content: center;
                                align-items: center;
                                position: relative;
                                padding: 28px 8px;
                                display: none;
                                &:before {
                                    content: '';
                                    width: 10px;
                                    height: 10px;
                                    background-color: #808695;
                                    border-radius: 50%;
                                    position: absolute;
                                    top: 50%;
                                    left: 50%;
                                    transform: translate(-50%, -50%);
                                }
                                &:hover {
                                    cursor: pointer;
                                    background-color: transparent;
                                    &:before {
                                        background-color: #30394A;
                                    }
                                }
                            }
                            &.active {
                                .node-collaspe {
                                    &:before {
                                        background-color: var(--btn-themes);
                                    }
                                }
                            }
                        }
                        .text-menu {
                            font-size: 14px;
                            display: block;
                            padding: 8px 16px !important;
                        }
                    }
                }
                li {
                    a {
                        padding: 19px 16px;
                        .icon-arrown {
                            display: none;
                        }
                    }
                }
            }
        }
        .content {
            padding-left: 56px;
        }
        /*Content*/
        .content {
            .navbar {
                margin-left: -72px;
            }
            .logo {
                opacity: 1;
                visibility: visible;
                width: 103px;
                margin: 0 0 0 16px;
            }
        }
    }
    .content {
        padding-left: 220px;
        transition: all .3s ease;
        @media only screen and (max-width: 600px) {
            padding-left: 0;
        }
    }
}

#sidebar {
    min-width: 220px;
    max-width: 220px;
    background: #fff;
    color: #080C12;
    transition: all .3s ease;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 999;
    @media only screen and (max-width: 600px) {
        top: 48px;
    }
}

#sidebar .sidebar-header {
    padding: 8px 10px;
}

#sidebar ul.components {
    padding: 20px 0px;
    overflow: auto;
    max-height: calc(100vh - 219px);
    .list-unstyled {
        &.show {
            li {
                &.active {
                    a {
                        font-weight: 600;
                        color: var(--btn-themes);
                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}

#sidebar ul p {
    padding: 10px;
    font-size: 15px;
    display: block;
}

#sidebar ul li a {
    padding: 10px 16px;
    font-size: .875rem;
    display: block;
    [class^="icon-"] {
        font-size: 17.5px;
        transition: all .3s ease;
    }
    &.collapsed {
        .icon-arrown {
            transform: rotate(90deg);
        }
    }
    &.node-collaspe {
        display: none;
    }
    &[aria-expanded="fasle"] {
        .icon-arrown {
            transform: rotate(90deg);
        }
    }
    &[aria-expanded="true"] {
        .icon-arrown {
            transform: rotate(0deg);
        }
    }
}

#sidebar ul li a:hover {
    color: #fff;
    background: #034EA2;
    [class^="icon-"] {
        &:before {
            color: #fff;
        }
    }
}

#sidebar ul li>a[aria-expanded="true"] {
    /* color: #fff;
    background: #034EA2; 
    [class*="icon"] {
        &:before {
            color: #fff;
        }
    }*/
}

#sidebar ul li>a.active {
    color: #fff;
    background: #034EA2;
    [class*="icon"] {
        &:before {
            color: #fff;
        }
    }
}

a[data-toggle="collapse"] {
    position: relative
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%)
}

ul ul a {
    padding-left: 48px !important;
}

ul.CTAs {
    padding: 20px
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px
}

a.download,
a.download:hover {
    background: #318fb5;
    color: #fff;
}

.list-unstyled {
    .fa-chevron-down {
        transition: all .2s ease;
    }
    [aria-expanded="true"] {
        .fa-chevron-down {
            transform: rotate(180deg);
        }
    }
}

@media(maz-width:768px) {
    #sidebar {
        margin-left: -250px
    }
    #sidebar.active {
        margin-left: 0px
    }
    #sidebarCollapse span {
        display: none
    }
}