/*Block Calender*/

.box-date-time {
    font-weight: 500;
    font-size: 24px;
    line-height: 160%;
    color: #000;
    border-bottom: 1px solid #e5e5e5;
    position: relative;
    @media only screen and (max-width: 1280px) {
        flex-wrap: wrap;
    }
    .icon-arrow1 {
        &:before {
            color: #808695;
        }
        &:first-child {
            transform: rotate(180deg);
        }
        &:hover {
            cursor: pointer;
            &:before {
                color: #212733;
            }
        }
    }
    .date {
        width: auto;
        input {
            display: none;
        }
        .icon-calendar {
            font-size: 31px;
        }
    }
    .box-node {
        position: absolute;
        top: 10px;
        left: 0;
        max-width: 360px;
        @media only screen and (max-width: 1280px) {
            position: static;
            margin-left: 0;
            margin-right: auto;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 0;
            padding-left: 0;
        }
        &.inline {
            max-width: unset;
        }
        .item {
            padding-left: 20px;
            position: relative;
            list-style-type: none;
            font-size: 12px;
            line-height: 14px;
            text-align: center;
            color: #515A6E;
            margin-left: 15px;
            margin-bottom: 8px;
            text-align: left;
            &:before {
                content: '';
                border-radius: 50%;
                width: 12px;
                height: 12px;
                position: absolute;
                left: 0;
                top: 0;
            }
            &.time {
                width: 80px;
                &:before {
                    background-color: #1983FC;
                }
            }
            &.off-approve {
                &:before {
                    background-image: repeating-linear-gradient(135deg, #4a9dfc, #4a9dfc 2px, #6eb1fd 2px, #6eb1fd 4px), linear-gradient(to bottom, #eee, #999) !important;
                }
            }
            &.off-not-approve {
                &:before {
                    background-image: repeating-linear-gradient(135deg, #ff645c, #ff645c 2px, #ff837d 2px, #ff837d 4px), linear-gradient(to bottom, #eee, #999) !important;
                }
            }
            &.late {
                width: 80px;
                &:before {
                    background-color: #FF3C31;
                }
            }
            &.day-off {
                &:before {
                    background-image: repeating-linear-gradient(135deg, #b7b7b7, #b7b7b7 2px, #d4d4d4 2px, #d4d4d4 4px), linear-gradient(to bottom, #eee, #999) !important;
                }
            }
            &.node-interview {
                width: unset;
                &.offline {
                    &:before {
                        background-color: #1983FC;
                    }
                }
                &.online {
                    &:before {
                        background-color: #F37021;
                    }
                }
            }
        }
    }
    .group-button {
        position: absolute;
        right: 0;
        @media only screen and (max-width: 1280px) {
            position: static;
        }
        span,
        .far {
            font-weight: 400;
            font-size: 0.875rem;
            line-height: 160%;
            &:hover {
                color: green;
            }
        }
        .far {
            font-size: 1rem;
            color: green;
        }
    }
}

.block-calendar {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-gap: 8px 8px;
    color: #515A6E;
    transition: all .2s ease;
    overflow: auto;
    @media only screen and (max-width: 1600px) {
        grid-gap: 5px 5px;
    }
    &.interview {
        .box-days {
            border: 1px solid #E8EAEC;
            border-radius: 2px;
            background: transparent;
            min-height: 160px;
            &.header {
                min-height: unset;
                border: 0;
            }
            .wrap-item {
                overflow: auto;
                max-height: 324px;
                .box-item {
                    position: relative;
                    padding: 6px 6px 6px 10px;
                    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
                    &.offline {
                        border-left: 3px solid #1983FC;
                        border-radius: 2px 0px 0px 2px;
                        &.show {
                            background-color: var(--text-link);
                            .tooltip-view {
                                opacity: 1;
                                visibility: visible;
                            }
                            .name {
                                color: #fff;
                            }
                            .hour {
                                color: rgba(255, 255, 255, 0.7);
                            }
                        }
                    }
                    &.online {
                        border-left: 3px solid #F37021;
                        border-radius: 2px 0px 0px 2px;
                        &.show {
                            background-color: #F37021;
                            .tooltip-view {
                                opacity: 1;
                                visibility: visible;
                            }
                            .name {
                                color: #fff;
                            }
                            .hour {
                                color: rgba(255, 255, 255, 0.7);
                            }
                        }
                        .tooltip-view {
                            .box-top {
                                .title {
                                    &:before {
                                        background-color: #F37021;
                                    }
                                }
                            }
                        }
                    }
                    .name {
                        font-size: 12px;
                        font-weight: 500;
                        margin-bottom: 4px;
                        line-height: 14px;
                        color: #14142B;
                    }
                    .hour {
                        font-size: 12px;
                        line-height: 14px;
                        color: var(--text-title);
                    }
                    .tooltip-view {
                        max-width: 310px;
                        background-color: #fff;
                        position: fixed;
                        left: 0;
                        top: 0;
                        box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
                        padding: 25px 15px;
                        z-index: 3;
                        opacity: 0;
                        visibility: hidden;
                        transition: all .3s ease;
                        &:before {
                            content: "";
                            position: absolute;
                            width: 0;
                            height: 0;
                            top: 20px;
                            left: 0;
                            border: 0.45em solid black;
                            border-color: transparent transparent #fff #fff;
                            transform: rotate(45deg);
                            box-shadow: -3px 3px 3px 0 rgba(0, 0, 0, .06);
                        }
                        .button-options {
                            position: absolute;
                            top: 8px;
                            right: 16px;
                        }
                        .box-top {
                            padding-left: 16px;
                            .title {
                                position: relative;
                                &:before {
                                    content: '';
                                    background-color: #1983FC;
                                    width: 8px;
                                    height: 8px;
                                    border-radius: 1px;
                                    position: absolute;
                                    top: 6px;
                                    left: -16px;
                                }
                            }
                            .date-time {
                                color: var(--text-title);
                            }
                            .view-file {
                                color: var(--text-link);
                            }
                        }
                        .box-bottom {
                            border-top: 1px solid #E5E5E5;
                            .box-info {
                                .label {
                                    font-size: 12px;
                                    color: #808695;
                                }
                                .text {
                                    color: var(--text-themes);
                                }
                            }
                        }
                    }
                }
            }
            .view-more {
                .icon-menu-dots {
                    transform: rotate(90deg);
                }
            }
        }
    }
    .box-days {
        padding: 10px 15px;
        background: #EBF4FF;
        border-radius: 2px;
        transition: all .2s ease;
        min-width: 145px;
        @media only screen and (max-width: 1600px) {
            padding: 10px 8px;
        }
        &.header {
            font-weight: 500;
            font-size: 16px;
            line-height: 160%;
            background: transparent;
            text-align: center;
            padding: 4px;
        }
        &.pass {
            border: 1px solid #E8EAEC;
            background: transparent;
            .date {
                span {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 160%;
                    color: #E8EAEC;
                }
            }
        }
        &.check-in-late {
            background-color: rgba(255, 60, 49, .1);
            &.overload {
                background-image: repeating-linear-gradient( 135deg, #FFECEB, #FFECEB 10px, #FFF5F5 10px, #FFF5F5 20px), linear-gradient(to bottom, #eee, #999) !important;
            }
            .date-time {
                .time {
                    background-color: #FF3C31;
                }
            }
        }
        &.overload {
            background-image: repeating-linear-gradient( 135deg, #EBF4FF, #EBF4FF 10px, #F5F9FF 10px, #F5F9FF 20px), linear-gradient(to bottom, #eee, #999) !important;
        }
        &.days-off {
            background-image: repeating-linear-gradient( 135deg, #F7F7FD, #F7F7FD 10px, #FBFBFE 10px, #FBFBFE 20px), linear-gradient(to bottom, #eee, #999) !important;
            position: relative;
            span {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                color: rgba(128, 134, 149, 0.5);
            }
        }
        .date-time {
            align-items: center;
            .date {
                font-weight: 500;
                font-size: 18px;
                color: #515A6E;
                margin-right: 8px;
                @media only screen and (max-width: 1600px) {
                    font-size: 14px;
                }
            }
            .time {
                margin-left: auto;
                font-weight: 400;
                font-size: 14px;
                color: #FFFFFF;
                background: #1983FC;
                opacity: 0.8;
                border-radius: 40px;
                padding: 5px 15px;
                display: flex;
                align-items: center;
                @media only screen and (max-width: 1720px) {
                    padding: 5px 8px;
                }
                @media only screen and (max-width: 1600px) {
                    padding: 5px 3px;
                    font-size: 13px;
                }
            }
            [class*="icon"] {
                &:before {
                    color: #fff;
                }
            }
            .text {
                font-size: 14px;
                white-space: nowrap;
                transition: all .3s ease;
                @media only screen and (max-width: 1480px) {
                    font-size: 12px;
                }
            }
        }
        .time-off {
            padding-top: 10px;
            margin-top: 12px;
            border-top: 1px solid rgba(20, 20, 43, 0.1);
            .off {
                display: flex;
                align-items: center;
                flex-direction: column;
                width: 100%;
                .hour {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 160%;
                    color: #515A6E;
                }
                [class*="icon-"] {
                    font-size: 16px;
                }
            }
            .duration {
                display: flex;
                align-items: center;
                flex-direction: column;
                width: 100%;
                position: relative;
                &:after {
                    content: '';
                    position: absolute;
                    top: 3px;
                    left: 0;
                    height: 14px;
                    width: 1px;
                    background-color: rgba(20, 20, 43, 0.1);
                }
                .hour {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 160%;
                    color: #515A6E;
                }
            }
        }
        .icon-check {
            &:before {
                color: #319E29;
            }
        }
        .icon-reject {
            &:before {
                color: #FF3C31;
            }
        }
    }
}