* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    background: var(--bg-themes);
    color: var(--text-themes);
    font-size: 0.875rem;
    line-height: 160%;
}

p {
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s
}

.form-control {
    border: 1px solid #e5e5e5;
}

.btn,
.form-control,
.form-select {
    font-size: 0.875rem;
    ;
}

a:hover {
    [class*="icon-"] {
        color: var(--text-link);
    }
    .icon-trash {
        color: #FF3C31;
    }
}