.steps {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    overflow-x: auto;
    width: 100%;
}

.steps .step:first-child {
    margin-left: auto;
}

.steps .step:last-child {
    margin-right: auto;
}

.step:first-of-type .step-circle::before {
    display: none;
}

.step:last-of-type .step-content {
    padding-right: 0;
}

.step-content {
    box-sizing: content-box;
    display: flex;
    align-items: center;
    flex-direction: column;
    min-width: 5rem;
    max-width: 5rem;
    padding-top: 0.5rem;
}

.step-circle {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1.125rem;
    height: 1.125rem;
    color: #adb5bd;
    border: 1px solid #adb5bd;
    border-radius: 100%;
    background-color: #fff;
    [class*="icon"] {
        font-size: 12px;
    }
}

.step-circle::before {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: -2px;
    width: calc(5rem + 1rem - 1.125rem);
    height: 2px;
    transform: translate(-100%, -50%);
    color: #adb5bd;
    background-color: currentColor;
}

.step-text {
    color: rgba(48, 57, 74, .5);
    word-break: break-all;
    margin-top: 0.25em;
    font-size: 0.75rem;
    white-space: nowrap;
}

.step-active .step-circle {
    color: #fff;
    background-color: #D6E9FF;
    border-color: #1983FC;
    [class*="icon"] {
        color: #1983FC;
    }
}

.step-active.pass .step-circle::before {
    color: #adb5bd;
}

.step-active .step-circle::before {
    color: #1983FC;
}

.step-active .step-text {
    color: #30394A;
}

.step-error .step-circle {
    color: #fff;
    background-color: #dc3545;
    border-color: #dc3545;
}

.step-error .step-circle::before {
    color: #dc3545;
}

.step-error .step-text {
    color: #dc3545;
}

.step-success .step-circle {
    color: #28a745;
    background-color: #fff;
    border-color: #28a745;
}

.step-success .step-circle::before {
    color: #28a745;
}

.step-success .step-text {
    color: #28a745;
}


