/*Badge themes*/

.badge {
    &.badge-themes {
        &.bg-default {
            color: #14142B;
            background-color: #E5E5E5 !important;
            input {
                background-color: #E5E5E5 !important;
                color: #14142B;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-info {
            color: #034EA2;
            background-color: #EBF4FF !important;
            input {
                background-color: #EBF4FF !important;
                color: #034EA2;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-success {
            color: #009A36;
            background-color: #E6F7E9 !important;
            input {
                background-color: #E6F7E9 !important;
                color: #009A36;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-warning {
            color: #F37021;
            background-color: #FEF3EC !important;
            input {
                background-color: #FEF3EC !important;
                color: #F37021;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-danger {
            color: #FF3C31;
            background-color: #FFE7E9 !important;
            input {
                background-color: #FFE7E9 !important;
                color: #FF3C31;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-primary {
            color: #6699FF;
            background-color: rgba(102, 204, 255, 0.498039215686275) !important;
            input {
                background-color: rgba(102, 204, 255, 0.498039215686275) !important;
                color: #6699FF;
                &:focus {
                    outline: none;
                }
            }
        }
        &.bg-pink {
            color: #E1A9FA;
            background-color: rgba(250, 231, 255, 1) !important;
            input {
                background-color: rgba(250, 231, 255, 1) !important;
                color: #E1A9FA;
                &:focus {
                    outline: none;
                }
            }
        }
        &.range {
            width: 24px;
            height: 24px;
            font-size: .875em;
            padding: 0.45em;
        }
    }
}


/*Label Themes*/

.label {
    &.label-themes {
        border-radius: 30px;
        padding: 4px 12px;
        &.label-success {
            background-color: #E6F7E9;
            color: #319E29 !important;
        }
        &.label-danger {
            background-color: rgba(255, 60, 49, 0.1);
            color: #FF3C31 !important;
        }
        &.label-warning {
            background-color: rgba(244, 190, 55, 0.1);
            ;
            color: #FAA71A !important;
        }
        &.label-primary {
            background-color: #D6E9FF;
            color: #1983FC !important;
        }
    }
}


/*Input themes*/

.input-primary {
    color: #034EA2;
    border: 1px solid #e5e5e5;
    border-radius: 2px;
    &:hover,
    &:focus {
        color: #034EA2;
        border: 1px solid #1983FC;
        outline: none;
        box-shadow: none;
    }
}

.input-success {
    color: #009A36;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    &:hover,
    &:focus {
        border: 1px solid #50B848;
        outline: none;
        box-shadow: none;
        color: #009A36;
    }
}


