@import "var.scss";
@import "reset.scss";
@import "select2.scss";
@import "menu.scss";
@import "form.scss";
@import "effect.scss";
@import "font.scss";
@import "component.scss";
@import "table.scss";
@import "modal.scss";
@import "calendar-themes.scss";
@import "checkin-user.scss";
@import "bootstrap-steps.scss";
@import "button.scss";

/* Scrollbar custom Works on Firefox */

* {
    scrollbar-width: thin;
    scrollbar-color: rgb(192, 192, 192) white;
}


/* Works on Chrome, Edge, and Safari */

*::-webkit-scrollbar {
    width: 12px;
}

*::-webkit-scrollbar-track {
    background: white;
}

*::-webkit-scrollbar-thumb {
    background-color: #E5E5E5;
    border-radius: 20px;
    border: 3px solid white;
}

.badge-notification {
    position: absolute;
    font-size: .6rem;
    margin-top: -0.1rem;
    margin-left: -0.5rem;
    padding: 0.2em 0.45em;
    background-color: #F37021;
}

#content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s
}

.content-wrapper {
    padding: 16px 16px 55px 16px;
    &.show-list-edit {
        .column-left-view {
            transform: translateX(calc(-100% - 20px));
        }
        .column-center-view {
            max-width: 100% !important;
            margin-left: -310px;
        }
        .view-candidate {
            >.ic-close {
                top: 22px;
                left: 30px;
            }
            .box-avatar {
                padding-left: 45px;
            }
        }
    }
    >.d-flex {
        position: relative;
    }
}

.content {
    width: 100%;
    .navbar {
        padding: 5px 10px;
        position: sticky;
        top: 0;
        z-index: 9999;
        .btn-collaspe {
            display: flex;
            align-items: center;
            padding: 3px 12px;
        }
        .icon-arrown,
        .icon-bell {
            &:before {
                color: #fff;
            }
        }
    }
    .logo {
        width: 0;
        margin: 0;
        opacity: 0;
        visibility: hidden;
        transition: all .2s ease;
        img {
            height: 38px;
        }
    }
}

.bg-themes {
    background-color: var(--bg-themes);
}

.bg-popup {
    background-color: var(--bg-popup);
}


/*Text Color*/

.text-orange-themes {
    color: var(--text-orange-themes);
}

.text-primary-themes {
    color: var(--text-primary-themes);
}

.text-red-themes {
    color: var(--text-red-themes);
}

.btn-collaspe {
    .icon-menu {
        font-size: 24px;
        &:before {
            color: #fff;
        }
    }
}

.circle-down {
    border: 1.5px solid #515A6E;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.hidden-arrow {
    &.dropdown-toggle {
        &:after {
            display: none;
        }
    }
}

.dropdown-filter {
    border: 0;
    box-shadow: 0px 4px 25px rgba(81, 90, 110, 0.15);
    border-radius: 2px;
    padding: 0;
    overflow: auto;
}

.employee-number {
    .icon-card {
        font-size: 19px;
    }
}


/*Pagination*/

.pagination {
    .page-item {
        margin-right: 4px;
        &:last-child {
            margin-right: 0;
        }
        &.active {
            .page-link {
                color: #fff;
                background-color: #034EA2;
                border: 1px solid #034EA2;
            }
        }
        .page-link {
            border-radius: 2px;
            border: 1px solid #E5E5E5;
            color: #515A6E;
            &:hover {
                background-color: #034EA2;
                border: 1px solid #034EA2;
                color: #fff;
            }
        }
    }
}


/*Box filter*/

.box-filter {
    .dropdown-menu {
        .dropdown-item {
            padding: 0.4375rem;
            font-size: 0.9375rem;
            line-height: 1.5rem;
            &:hover {
                background-color: #5897fb;
                color: #fff;
                font-weight: 500;
            }
        }
    }
    .dropdown-scrollbar {
        .dropdown-menu {
            max-height: 370px;
            position: relative;
        }
    }
    .icon-filter {
        font-size: 18px;
    }
    .icon-add {
        &:before {
            color: #064996;
        }
    }
    form {
        >div {
            @media screen and (max-width: 480px) {
                width: 100%;
            }
            .dropdown-toggle,
            .dropdown-link {
                @media screen and (max-width: 480px) {
                    width: 100%;
                    text-align: left;
                }
            }
        }
    }
}

.dropdown-filter {
    .search {
        padding: 0.4375rem;
        .box-search {
            background-color: #F7F7FD;
            border-radius: 4px;
            position: relative;
            .icon-search {
                position: absolute;
                left: 10px;
                top: 6px;
                font-size: 24px;
            }
            input {
                background-color: #F7F7FD;
                padding-left: 40px;
                border: 0;
            }
        }
    }
    .group {
        background-color: var(--bg-popup);
        a {
            color: var(--text-themes-light);
            font-weight: 500;
        }
    }
}

.dropdown-scrollbar {
    position: absolute;
    &.dropdown-menu {
        overflow: auto;
        .search {
            position: sticky;
            top: 0;
            background: #fff;
            .box-search {
                display: block !important;
                padding: 0;
            }
        }
    }
    /*  &:after{
        content: "";
        width: 100%;
        height: 65px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 61.87%);
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 1000;
        pointer-events: none;
    } */
}


/*Box Right*/

.box-right {
    position: fixed;
    right: -100%;
    top: 48px;
    height: calc(100% - 48px);
    width: 100%;
    max-width: 280px;
    background-color: #fff;
    z-index: 1000;
    padding: 24px;
    overflow: auto;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    transition: all .5s ease;
    @media only screen and (max-width: 1600px) {
        max-width: 220px;
        padding: 16px;
    }
    &.active {
        right: 0;
    }
    .text-title {
        font-size: 0.875em;
        font-weight: 400;
    }
    .dropdown-toggle,
    .form-control,
    .input-group {
        border: 1px solid #e5e5e5;
        border-radius: 0.25rem;
    }
}


/*Box Layer*/

.box-layer {
    position: fixed;
    right: -100%;
    top: 46px;
    height: calc(100% - 46px);
    width: 100%;
    max-width: calc(100% - 220px);
    z-index: 9;
    overflow: auto;
    transition: all .3s ease;
    background-color: var(--bg-themes);
    &.active {
        right: 0;
        &.collaspe {
            max-width: calc(100% - 56px);
        }
    }
}

.column-left-view {
    width: 100%;
    transition: all .3s ease;
    @media screen and (max-width: 992px) {
        max-width: 100% !important;
    }
    &.range-user {
        .list-notify-department {
            li {
                color: #14142B;
                a {
                    font-weight: 500;
                    color: #14142B;
                }
            }
        }
    }
}

.column-center-view {
    width: 100px;
    transition: all .3s ease;
    @media screen and (max-width: 992px) {
        max-width: 100% !important;
    }
    &.view-user {
        .box-title {
            .ratings {
                margin-bottom: 2px;
            }
        }
        @media screen and (max-width: 992px) {
            max-width: 100% !important;
        }
    }
}

.column-right-view {
    padding: 24px;
    background-color: #fff;
    width: 100%;
    transition: all .3s ease;
    @media screen and (max-width: 992px) {
        max-width: 100%;
    }
    .title {
        color: #212733;
        font-size: 1.125rem;
    }
}

.box-range {
    .box-inner {
        border-radius: 2px;
        color: #515A6E;
        padding: 9px 0;
        .range {
            width: 24px;
            height: 24px;
        }
    }
}

.wrap-user {
    height: 100%;
    display: flex;
    flex-direction: column;
    .title {
        flex-shrink: 0;
    }
    .wrap-inner-user {
        display: flex;
        flex-wrap: wrap;
        flex: 1;
        width: 100%;
        align-items: center;
    }
    .box-inner-user {
        display: flex;
        flex-wrap: wrap;
    }
}

.box-user {
    display: flex;
    .icon {
        min-width: 56px;
        height: 56px;
        border: 1px solid #f7f7fd;
        background-color: #f7f7fd;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 12px;
        font-size: 22px;
    }
    .info {
        display: flex;
        flex-wrap: wrap;
        .text {
            width: 100%;
            color: #808695;
            font-size: 14px;
        }
        .name {
            margin-right: 12px;
        }
    }
}

.box-info {
    @media screen and (max-width: 480px) {
        width: 100%;
        margin-bottom: 8px;
    }
    .label {
        color: #515A6E;
        font-size: 14px;
        display: block;
    }
    .text {
        font-size: .875rem;
        color: #14142B;
    }
}

.list-notify-department {
    ul {
        padding-left: 24px;
        li {
            position: relative;
            color: #515A6E;
            list-style-type: none;
            &:before {
                content: '';
                background-color: #808695;
                border-radius: 50%;
                width: 6px;
                height: 6px;
                position: absolute;
                left: -19px;
                top: 8px;
            }
            a {
                color: #1983FC;
            }
        }
    }
    .note-status {
        font-weight: 500;
        font-size: 12px;
        color: #515A6E;
    }
}

.dropdown-list {
    position: absolute;
    right: 0;
    z-index: 999;
    width: 200px;
    background-color: #fff;
    box-shadow: 0px 4px 25px rgba(81, 90, 110, 0.15);
    border-radius: 2px;
    ul {
        padding-left: 0;
        margin: 0;
        li {
            padding: 10px 15px;
            color: #515A6E;
            font-size: 14px;
            list-style-type: none;
        }
    }
}

.box-filter-list,
.box-filter-dropdown {
    display: flex;
    position: relative;
    .title {
        font-weight: 500;
        color: #14142B;
        padding: 20px 15px;
        font-size: 14px;
        border-bottom: 1px solid #E5E5E5;
    }
    .dropdown-list {
        top: 40px;
        left: 0;
        right: auto;
        width: 240px;
        max-height: 670px;
        ul {
            overflow: auto;
            max-height: calc(100vh - 400px);
        }
    }
    .icon-menu {
        font-size: 22px;
    }
}

.note-attach-file {
    font-size: 12px;
    color: #515A6E;
    padding-left: 30px;
    position: relative;
    &:before {
        content: '';
        background: url(images/icons/attach-file.svg) no-repeat;
        width: 14px;
        height: 15px;
        position: absolute;
        left: 6px;
        top: 2px;
    }
}

.box-ratings {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 15px;
    width: 100%;
    .box-rate {
        max-width: 145px;
        width: 100%;
        border-radius: 6px;
        padding: 15px;
        @media screen and (max-width: 1600px) {
            padding: 10px;
        }
        .rate {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 16px;
            color: #fff;
            @media screen and (max-width: 1600px) {
                width: 32px;
                height: 32px;
                margin-bottom: 12px;
            }
        }
        .num {
            display: block;
            font-weight: 500;
            font-size: 28px;
            @media screen and (max-width: 1600px) {
                font-size: 20px;
            }
            sup {
                font-size: 12px;
                font-weight: 400;
                top: -1.3em;
                left: -0.5em;
                @media screen and (max-width: 1600px) {
                    top: -1em;
                    left: -0.2em;
                }
            }
        }
        .text {
            font-size: 12px;
            color: #515A6E;
        }
        &.rate-a {
            background-color: #E6F7E9;
            .rate {
                background-color: #53CA6D;
            }
        }
        &.rate-b {
            background-color: #EBF4FF;
            .rate {
                background-color: #4A9DFC;
            }
        }
        &.rate-c {
            background-color: #FEF3EC;
            .rate {
                background-color: #F59356;
            }
        }
        &.rate-d {
            background-color: #FFEBEE;
            .rate {
                background-color: #F94E4D;
            }
        }
    }
}


/*Fixed Button Bottom*/

.button-group-bottom {
    position: fixed!important;
    width: 100%;
    left: 0;
    bottom: 0;
    z-index: 9;
    background-color: #f7f7f7;
    padding: 10px 20px;
}


/*Multitab*/

.multitab-themes {
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow-y: hidden;
    &.nav-tabs .nav-item.show .nav-link,
    &.nav-tabs .nav-link.active {
        background-color: transparent;
        border-color: transparent;
    }
    &.nav-tabs .nav-link:focus,
    &.nav-tabs .nav-link:hover {
        border-color: transparent;
    }
    .nav-link {
        color: #808695;
        padding-left: 0;
        padding-right: 0;
        margin-right: 25px;
        position: relative;
        &.active {
            color: #034EA2;
            font-weight: 500;
            &::after {
                content: '';
                width: 100%;
                height: 2px;
                background-color: #034EA2;
                position: absolute;
                left: 0;
                bottom: -1px;
            }
        }
    }
}

.general-information {
    .box-item {
        background: #F7F7FD;
        border-radius: 2px;
        padding: 10px 25px;
        position: relative;
        .label {
            font-size: 14px;
            line-height: 160%;
            color: #808695;
        }
        .days {
            font-weight: 500;
            font-size: 28px;
            line-height: 160%;
            color: #17233D;
        }
        .percent {
            position: absolute;
            right: 12px;
            top: 10px;
            min-width: 60px;
            text-align: center;
            padding: 2px 11px;
            border-radius: 2px;
            font-weight: 500;
        }
    }
}

.wrap-form {
    &.user-late {
        .col-left {
            width: 60%;
            padding: 0 16px;
            transition: all .3s ease;
            @media screen and (max-width: 1279px) {
                width: 100%;
                margin-bottom: 24px;
            }
            @media screen and (max-width: 767px) {
                padding: 0;
            }
            .form-select {
                border-radius: 0;
                &:not(:last-child) {
                    border-right: 0;
                }
                &:first-child {
                    border-top-left-radius: .25rem;
                    border-bottom-left-radius: 0.25rem;
                }
                &:last-child {
                    border-top-right-radius: .25rem;
                    border-bottom-right-radius: .25rem;
                }
                &:hover {
                    cursor: pointer;
                }
                &:focus {
                    box-shadow: none;
                    border-color: #ced4da;
                }
            }
        }
        .col-right {
            border-left: 1px solid #e5e5e5;
            width: 40%;
            padding: 0 16px;
            transition: all .3s ease;
            @media screen and (max-width: 1279px) {
                width: 100%;
                border-left: 0;
            }
            @media screen and (max-width: 767px) {
                padding: 0;
            }
            .box-info {
                max-width: 310px;
                margin: 0 auto;
                @media screen and (max-width: 1279px) {
                    max-width: 100%;
                    margin: 0;
                }
                .lbl {
                    color: #808695;
                }
                &-checkin {
                    background-color: #f7f7fd;
                    border-radius: 2px;
                    padding: 15px 12px;
                    .item {
                        .lbl {
                            color: #808695;
                            line-height: 160%;
                        }
                        .value {
                            font-weight: 500;
                            font-size: 16px;
                            color: #30394A;
                        }
                    }
                }
            }
        }
        &.form-checkin {
            .col-left,
            .col-right {
                width: 50%;
                @media screen and (max-width: 1279px) {
                    width: 100%;
                }
            }
            .col-right {
                .box-hour {
                    @media screen and (max-width: 1500px) {
                        flex-wrap: wrap;
                    }
                    @media screen and (max-width: 1199px) {
                        flex-wrap: nowrap;
                    }
                    @media screen and (max-width: 419px) {
                        flex-wrap: wrap;
                    }
                    .box-start {
                        margin-right: 16px;
                        @media screen and (max-width: 1500px) {
                            margin-right: 0;
                            margin-bottom: 16px;
                        }
                        @media screen and (max-width: 1199px) {
                            margin-right: 16px;
                            margin-bottom: 0;
                        }
                        @media screen and (max-width: 419px) {
                            margin-right: 0;
                            margin-bottom: 16px;
                        }
                    }
                    .form-select {
                        padding: 0.375rem 0.625rem;
                        background-position: right 0.25rem center;
                        border-radius: 0;
                        &:not(:last-child) {
                            border-right: 0;
                        }
                        &:first-child {
                            border-top-left-radius: .25rem;
                            border-bottom-left-radius: 0.25rem;
                        }
                        &:last-child {
                            border-top-right-radius: .25rem;
                            border-bottom-right-radius: .25rem;
                        }
                        &:hover {
                            cursor: pointer;
                        }
                        &:focus {
                            box-shadow: none;
                            border-color: #ced4da;
                        }
                    }
                }
            }
        }
    }
    .form-user {
        &.late {
            max-width: 470px;
            margin: 0 auto;
            @media screen and (max-width: 1279px) {
                max-width: 100%;
                margin: 0;
            }
        }
        &.checkin {
            .note {
                color: #808695;
                display: block;
            }
            .text {
                font-weight: 500;
            }
            .datetimepicker {
                .header {
                    .ms-auto {
                        a {
                            &:first-child {
                                [class*="icon"] {
                                    transform: rotate(180deg);
                                }
                            }
                            &:hover,
                            &.active {
                                [class*="icon"] {
                                    &:before {
                                        color: #000;
                                    }
                                }
                            }
                        }
                        [class*="icon"] {
                            font-size: 30px;
                            &:before {
                                color: rgba(0, 0, 0, .3);
                            }
                        }
                    }
                }
                .table {
                    border: 1px solid #e5e5e5;
                    th {
                        text-align: center;
                    }
                    td {
                        text-align: center;
                        border-left: 1px solid #e5e5e5;
                        &:last-child {
                            border-right: 1px solid #e5e5e5;
                        }
                        &:hover {
                            cursor: pointer;
                        }
                        &.day-off {
                            background-image: linear-gradient(to right, #E8EAEC 0%, #E8EAEC 0%);
                            background-repeat: no-repeat;
                            /* color: #fff; */
                            background-size: 100% 100%;
                            &.approve {
                                background-image: linear-gradient(to right, #1983FC 0%, #1983FC 0%);
                            }
                            &.half-day {
                                background-size: 50% 100%;
                                color: inherit;
                                &.afternoon {
                                    background-position: right top;
                                }
                            }
                            &.hour {
                                position: relative;
                                &:after {
                                    content: "\f017";
                                    font-family: "Font Awesome 6 Free";
                                    font-weight: 400;
                                    -moz-osx-font-smoothing: grayscale;
                                    -webkit-font-smoothing: antialiased;
                                    display: inline-block;
                                    font-style: normal;
                                    font-variant: normal;
                                    text-rendering: auto;
                                    line-height: 1;
                                    position: absolute;
                                    right: 4px;
                                    top: 4px;
                                    font-size: 10px;
                                }
                            }
                        }
                        &.active {
                            background-color: var(--btn-default-active);
                        }
                    }
                }
            }
            .box-node {
                .node {
                    position: relative;
                    padding-left: 16px;
                    &::before {
                        content: '';
                        width: 8px;
                        height: 8px;
                        position: absolute;
                        left: 0;
                        top: 6px;
                        border-radius: 50%;
                        border: 1px solid #1983FC;
                        background: transparent;
                    }
                    &.all-day {
                        &::before {
                            background-color: #1983FC;
                        }
                    }
                    &.half-day {
                        &::before {
                            background-image: linear-gradient(to right, #1983FC 0%, #1983FC 0%);
                            background-repeat: no-repeat;
                            background-size: 50% 100%;
                            background-position: right top;
                        }
                        &.afternoon {
                            &::before {
                                background-position: left top;
                            }
                        }
                    }
                }
            }
        }
    }
}

.box-node {
    display: flex;
    align-items: center;
    .node {
        position: relative;
        display: block;
        padding-left: 16px;
        &:not(:last-child) {
            margin-right: 16px;
        }
        &:before {
            content: '';
            width: 8px;
            height: 8px;
            background: #1983FC;
            border-radius: 100%;
            position: absolute;
            left: 0;
            top: 6px;
        }
        &.active {
            &:before {
                background: #034EA2;
            }
        }
    }
}

.box-data {
    .node {
        padding-left: 20px;
        position: relative;
        &:before {
            content: '';
            width: 15px;
            height: 15px;
            position: absolute;
            top: 0;
            left: 0;
        }
        &.node-a {
            &:before {
                background-color: #198754
            }
        }
        &.node-b {
            &:before {
                background-color: #0ECAF0;
            }
        }
        &.node-c {
            &:before {
                background-color: #FFC108;
            }
        }
        &.node-d {
            &:before {
                background-color: #DC3545;
            }
        }
    }
}

.status {
    position: relative;
    padding-left: 10px;
    &:before {
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #319E29;
        position: absolute;
        left: 0;
        top: 5px;
    }
    &.out {
        &:before {
            background-color: #FF3C31;
        }
    }
}

.form-add-user {
