    }
}


/*Dropdown scrollbar*/

.dropdown-scrollbar {
    &.phongban {
        max-height: calc(100vh - 300px);
    }
}


/*Custom Input File Upload*/

.text-formal-upload {
    font-size: 12px;
    line-height: 160%;
    color: #808695;
    font-style: italic;
}

.box-upload-file {
    display: flex;
    .inputfile {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
        +label {
            font-size: 12px;
            text-overflow: ellipsis;
            white-space: nowrap;
            cursor: pointer;
            display: inline-block;
            overflow: hidden;
            padding: 0.395rem 1.25rem;
            color: #515A6E;
            background-color: #fff;
            width: 100%;
            display: flex;
            align-items: center;
            * {
                pointer-events: none;
            }
            [class*="icon"] {
                font-size: 14px;
            }
        }
    }
}

.wrap-avatar-upload {
    position: relative;
    width: 35px;
    height: 35px;
    .icon-close {
        position: absolute;
        top: -7px;
        right: -12px;
        font-size: .6rem;
        background: #fff;
        padding: 2px;
        border-radius: 100%;
        border: 1px solid transparent;
        line-height: normal;
        transition: all .3s ease;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
            border: 1px solid red;
            color: red;
            cursor: pointer;
        }
    }
}

.box-avatar-upload {
    height: 100%;
    border: 1px dashed #1983FC;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    [class*="icon"] {
        color: #1983FC;
        font-size: 22px;
    }
}

.head-breadcrumb {
    color: #fff;
    @media screen and (max-width: 480px) {
        display: none !important;
    }
    .breadcrumb-item {
        &::before {
            color: #fff;
        }
        &.active {
            color: #ffb02c;
        }
    }
}


/*Listing Kanban*/

.listing-kanban {
    overflow: auto;
    .card {
        width: 100%;
        min-width: 220px;
    }
    .title {
        color: var(--btn-themes);
        border-bottom: 1px dashed;
    }
    .wrap-item {
        height: calc(100vh - 245px);
        overflow: auto;
        @media screen and (max-width: 1740px) {
            height: calc(100vh - 304px);
        }
        .box-item {
            border: 1px solid #E5E5E5;
            border-radius: 4px;
            .box-avatar {
                position: relative;
                .box-options {
                    position: absolute;
                    top: 5px;
                    right: 0;
                    &.active {
                        .list {
                            display: block;
                        }
                    }
                    [class*="icon"] {
                        font-size: 16px;
                    }
                    .list {
                        background: #FFFFFF;
                        box-shadow: 0px 4px 25px rgba(81, 90, 110, 0.15);
                        border-radius: 2px;
                        position: absolute;
                        right: 0;
                        top: 20px;
                        z-index: 1;
                        width: 100%;
                        min-width: 185px;
                        padding: 8px 0;
                        display: none;
                        li {
                            list-style-type: none;
                            padding: 10px 15px;
                        }
                    }
                }
            }
        }
    }
}


/*View ứng viên*/

.wrap-info-candidate {
    border-right: 1px solid #E5E5E5;
    width: 100%;
    max-width: 445px;
    .step-content {
