.box-info-candidate {
    /*.box-contact {
        background: #F5F5F5;
        border-radius: 2px;
        padding: 8px 16px;
    }*/
    .box-info {
        .label {
            color: #808695;
        }
    }
}

.frame-upload-CV {
    min-height: 370px;
    .box-upload-CV {
        margin: 0 auto;
        max-width: 260px;
    }
}

.view-candidate {
    position: relative;
    >.ic-close {
        position: absolute;
        top: 20px;
        left: 12px;
        z-index: 1;
        font-size: 24px;
