}

.list-ungvien {
    max-height: 300px;
    .search {
        position: sticky;
        top: 0;
        background-color: #fff;
    }
}

.hrm_insert_update_calendar {
    .box-filter {
        .badge-avatar {
            position: absolute;
            top: -2px;
            width: 26px;
            height: 26px;
            display: flex;
            right: -38px;
        }
    }
}

.box-view-cv {
    background: #e5e5e5;
    height: calc(100% + 50px);
}


/*Box Notify*/

.navbar {
    .box-notify {
        @media screen and (max-width: 575px) {
            right: -60px;
        }
    }
}

.box-notify {
    min-width: 450px;
    overflow: auto;
    max-height: calc(100vh - 425px);
    @media screen and (max-width: 575px) {
        min-width: 320px;
        max-height: calc(100vh - 88px);
    }
    .item {
        transition: all .3s ease;
        &.unseen {
            background-color: #efefef;
        }
        &:hover {
            cursor: pointer;
            background-color: #385472;
            color: #fff;
        }
        &:not(:last-of-type) {
            border-bottom: 1px solid rgba(0, 0, 0, .15);
        }
        &:nth-last-child(2) {
            border-bottom: 0;
        }
        &.view-all {
            position: sticky;
            bottom: 0;
            background: #fff;
            border-top: 1px solid rgba(0, 0, 0, .15);
            &:hover {
                color: var(--text-link);
            }
        }
        a {
            transition: none;
        }
    }
}

/*Loading*/
.loading{
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, .3);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    .fa-spinner{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 3rem;
        color: #0B2C52;
    }
}

/*Timeline Box*/

.outer-timeline-box {
    padding: 0 15px;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
}

.timeline-box {
    width: 100%;
    float: left;
    background: #fff;
    padding: 10px 10px 40px 0;
}

.timeline-bar {
    width: 100%;
    height: 4px;
    background: #ccc;
    position: relative;
    .neo-time {
        &.first-child {
            .txt-time,
            .txt-stt {
                transform: translateX(-10%);
            }
        }
        &.last-child {
            .txt-time,
            .txt-stt {
                transform: translateX(-100%);
            }
        }
    }
}

.timelin-progress {
    width: auto;
    height: 100%;
    position: absolute;
    background: #86B6FF;
    left: 0;
    top: 0;
}

.neo-time {
    width: 10px;
    height: 10px;
    border: 3px solid #aaa;
    display: block;
    position: absolute;
    border-radius: 100px;
    left: 0;
    top: -3px;
    background: #fff;
    cursor: pointer;
    z-index: 1;
    &:hover {
        .hints-time {
            opacity: 1;
            visibility: visible;
        }
    }
    &.finish {
        border-color: #326BFD;
    }
    &.waiting {
        border-color: #fc7b33;
    }
}

.txt-time {
    position: absolute;
    left: 50%;
    top: -30px;
    font-size: 11px;
    white-space: nowrap;
    transform: translateX(-50%)
}

.txt-stt {
    @extend .txt-time;
    top: auto;
    bottom: -30px;
}

.hints-time {
    position: relative;
    background: #6495d5;
    border: 2px solid #93def5;
    color: #fff;
    padding: 10px;
    top: -3px;
    left: 28px;
    width: auto;
    white-space: nowrap;
    visibility: hidden;
    opacity: 0;
    transition: .4s;
    z-index: 9;
    &:before,
    &:after {
        right: 100%;
        top: 50%;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
    }
    &:before {
        border-color: rgba(147, 222, 245, 0);
        border-right-color: #93def5;
        border-width: 13px;
        margin-top: -13px;
    }
    &:after {
        border-color: rgba(100, 149, 213, 0);
        border-right-color: #6495d5;
        border-width: 10px;
        margin-top: -10px;
    }
}

.box-note-timeline {
    position: relative;
    float: left;
    margin: 10px 0 10px 10px;
    .neo-time {
        position: relative;
        top: 5px;
        left: auto;
        float: left;
        &.finish,
        &.waiting {
            margin-right: 5px;
        }
    }
    span {
        float: left;
        margin-right: 10px;
    }
}

.node-time{
    position: relative;
    padding-left: 18px;
    &:before{
        content:'';
        width: 10px;
        height: 10px;
        position: absolute;
        left: 0;
        top: 3px;
        border-radius: 100%;
    }
    &.finish{
        &:before{
            background: #326BFD;
        }
    }
    &.waiting{
        &:before{
            background: #fc7b33;
        }
    }
}

.list-timeline{
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease;
    position: absolute;
    width: 100%;
    background: #fff;
    z-index: 1;
    top: 95px;
    right: 0;
    &.show{
        opacity: 1;
        visibility: visible;
    }
}