.checkin-user {
    .box-avatar {
        img {
            width: 24px;
            height: 24px;
        }
        .user-name {
            font-weight: 500;
        }
    }
    .node {
        width: 12px;
        height: 12px;
        background-color: #1983FC;
        border-radius: 50%;
        margin: 4px auto 0 auto;
        &.late {
            background-color: #FF3C31;
        }
        &.day-off {
            background-image: repeating-linear-gradient(135deg, #b7b7b7, #b7b7b7 2px, #d4d4d4 2px, #d4d4d4 4px), linear-gradient(to bottom, #eee, #999) !important;
        }
        &.off-approve {
            background-image: repeating-linear-gradient(135deg, #4a9dfc, #4a9dfc 2px, #6eb1fd 2px, #6eb1fd 4px), linear-gradient(to bottom, #eee, #999) !important;
        }
        &.off-not-approve {
            background-image: repeating-linear-gradient(135deg, #ff645c, #ff645c 2px, #ff837d 2px, #ff837d 4px), linear-gradient(to bottom, #eee, #999) !important;
        }
    }
    thead {
        th {
            text-align: center;
        }
    }
}